import React, { Component } from "react";
import { Button } from 'antd-mobile';
import { Slider, WingBlank, WhiteSpace} from 'antd-mobile';

import PubSub from "pubsub-js";
export default class SetGame extends Component {

    state = {
        autoEvolution:false,
        speed:400,
        size:20
    }

    // 
    setSize = (value) => {
        let { autoEvolution, speed, size } = this.state;
        size = value;
        this.setState({
            autoEvolution: autoEvolution,
            speed: speed,
            size: size
        })
        PubSub.publish('setSize', size)
    }
    // 
    setSpeed = (value) => {
        let {autoEvolution, speed, size}= this.state;
        speed = value;
        this.setState({ 
            autoEvolution: autoEvolution,
            speed: speed,
            size: size})
        // 
        if (autoEvolution === true)
        {
            clearInterval(this.timerID);
            this.timerID = setInterval(() => { PubSub.publish('evolution', 1) }, speed)
        }
    }

    // 
    stepEvolution = () => {
        let { autoEvolution, speed, size } = this.state;
        // 
        if (autoEvolution === true) {
            // 
            clearInterval(this.timerID);
            this.timerID = undefined
            // state
            this.setState({
                autoEvolution: false,
                speed: speed,
                size: size
            });
        }
        else
            PubSub.publish('evolution', 1)
    }
    // 
    setAutoEvolution = () => {
        let { autoEvolution, speed, size} = this.state;
        // 
        if (autoEvolution === false) {
            this.setState({
                autoEvolution: true,
                speed: speed,
                size: size
            });
            this.timerID = setInterval(() => { PubSub.publish('evolution', 1) }, speed)
        }
    }
    // 
    render() {
        let colorStep = (this.state.autoEvolution === true) ? "#ffffff" : "blue";
        let colorAuto = (this.state.autoEvolution === true) ? "blue" : "#ffffff";
        return (<div>
            <Button type="ghost" style={{ backgroundColor: colorStep }} onClick={this.stepEvolution}></Button>
            <WhiteSpace size="md"/>
            <Button type="ghost" style={{ backgroundColor: colorAuto }} onClick={this.setAutoEvolution}></Button>
            <WingBlank size="lg">
                <p className="sub-title"> : {this.state.size + "" + this.state.size}</p>
                <Slider
                    style={{ marginLeft: 30, marginRight: 30 }}
                    defaultValue={20}
                    min={3}
                    max={100}
                    onChange={this.setSize}
                />
            </WingBlank>
            <WhiteSpace size="md" />
            <WingBlank size="lg">
                <p className="sub-title"> : {this.state.speed + "ms"}</p>
                <Slider
                    style={{ marginLeft: 30, marginRight: 30 }}
                    defaultValue={400}
                    min={1}
                    max={800}
                    onChange={this.setSpeed}
                />
            </WingBlank>
            <WhiteSpace size="lg" />
            <WhiteSpace size="lg" />
        </div>)
    }
}


